import logo from './logo.jpg';
import information from './information.svg';
import project from './project.svg';
import work from './work.svg';
import fun from './fun.svg';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
} from "react-router-dom";
import Home from "./Home";
import WorkedAt from "./WorkedAt";
import Mastered from "./Mastered";
import Projects from "./Projects";
import Fun from "./Fun";
import YlafkihCoreApi from "./api/YlafkihCore";
import React, { useState, useEffect } from 'react';

function SidebarHeader(props) {
    return(
        <div className={"text-center"}>
            <Link to={props.link}><img src={props.profile_image} className={"object-contain rounded-full cursor-pointer mx-auto"} alt={"img"}/></Link>
            <h2 className={"text-white text-xl"}>{props.full_name}</h2>
            <h5 className={"text-white text-opacity-50 font-thin p-1"}>{props.profile_status_name}</h5>
        </div>

    );
}

function SidebarMenuItem(props) {
    return(
        <div className={"mt-5 ml-2 flex cursor-pointer hover:bg-white hover:bg-opacity-50"}>
            <img src={props.icon} className={"bg-white rounded-full p-1"} alt={"icon"}/>
            <Link to={props.link}><h1 className={"text-white text-center p-4 font-bold"}>{props.title}</h1></Link>
        </div>
    );
}

function MobileView(props){
    return (
        <Router>
            <div className={"flex"}>
                <div className={"sm:w-60 w-screen bg-black h-screen fixed flex flex-col overflow-y-scroll"}>
                    <SidebarHeader profile_image={props.data[0].profile_image} full_name={props.data[0].full_name} profile_status_name={props.data[0].profile_status_name} link={"/"}/>
                </div>
            </div>
        </Router>
    );
}

function DesktopView(props){
    return (
        <Router>
            <div className={"flex"}>
                <div className={"sm:w-60 w-screen bg-black h-screen fixed flex flex-col overflow-y-scroll"}>
                    <SidebarHeader profile_image={props.data[0].profile_image} full_name={props.data[0].full_name} profile_status_name={props.data[0].profile_status_name} link={"/"}/>
                    <SidebarMenuItem icon={work} title={"WorkedAt"} link={"/workedat"}/>
                    <SidebarMenuItem icon={information} title={"Mastered"} link={"/mastered"}/>
                    <SidebarMenuItem icon={project} title={"Projects"} link={"/projects"}/>
                    <SidebarMenuItem icon={fun} title={"Fun"} link={"/fun"}/>
                </div>
                <Switch>
                    <Route exact path="/">
                        <Home/>
                    </Route>
                    <Route exact path="/workedat">
                        <WorkedAt/>
                    </Route>
                    <Route exact path="/mastered">
                        <Mastered/>
                    </Route>
                    <Route exact path="/projects">
                        <Projects/>
                    </Route>
                    <Route exact path="/fun">
                        <Fun/>
                    </Route>
                </Switch>
            </div>
        </Router>
    );
}

function Sidebar() {
    const [data, setData] = useState(null);
    const [isMobile, setIsMobile] = useState(false);
  
    useEffect(() => {
      YlafkihCoreApi.get('/profiles')
        .then(response => {
          setData(response.data);
        })
        .catch(error => {
          console.error(error);
        });
    }, []);
  
    useEffect(() => {
      const checkMobileScreen = () => {
        if (window.innerWidth < 768) {
          setIsMobile(true);
        } else {
          setIsMobile(false);
        }
      };
  
      checkMobileScreen();
      window.addEventListener('resize', checkMobileScreen);
      return () => window.removeEventListener('resize', checkMobileScreen);
    }, []);
  
    if (data === null) {
        return (
            <div className="mx-auto mt-40">
              <div
                className="mx-auto mt-40"
                style={{
                  animation: 'spin 1s infinite',
                  width: '100px',
                  height: '100px',
                  border: '1px solid black',
                  borderRadius: '50%',
                  boxShadow: '0 20px 10px rgba(0, 0, 0, 0.1)',
                }}
              />
              <h1 className="text-center mt-4">Loading Data...</h1>
            </div>
          );
          
    }
    return (
      <div>
        {isMobile ? (
          <MobileView
           data={data}
          />
        ) : (
          <DesktopView
            data={data}
          />
        )}
      </div>
    );
  }
export default Sidebar;