import './App.css';
import Sidebar from "./Sidebar";
import React, {useEffect } from 'react';
import YlafkihCoreApi from "./api/YlafkihCore";

function App() {
  useEffect(() => {
    const interval = setInterval(() => {
      YlafkihCoreApi.get('/profiles')
        .then(response => {
        })
        .catch(error => {
          console.error(error);
        });
    }, 300000); // 300000 milliseconds = 5 minutes
  
    return () => clearInterval(interval);
  }, []);
  return (
        <Sidebar/>
  );
}

export default App;
