import Footer from "./Footer";
import GithubApi from './api/Github';
import React from "react";

function Projects() {
    const [projects, setProjects] = React.useState([]);
  
    React.useEffect(() => {
      GithubApi.get("/repos").then(res => {
        setProjects(res.data);
      });
    }, []); // added empty dependencies array to fix the infinite loop issue
  
    return (
      <div className={"contentW h-screen ml-60 flex flex-col h-screen justify-between"}>
        <section className="text-gray-600 body-font">
          <div className="container px-5 py-24 mx-auto">
            <div className="flex flex-wrap -mx-4 -mb-10 text-center">
            {projects.map(project => (
  <div key={project.id} className="sm:w-full md:w-1/2 mb-10 px-4 bg-white shadow-md hover:shadow-xl">
    <h2 className="text-gray-600 font-bold text-xl mt-6 mb-3">
      {project.name}
    </h2>
    <p className="leading-relaxed text-base">
      {project.description || "Must To Write A Description"}
    </p>
    <button className="flex mx-auto mt-6 mb-6 text-white bg-gray-500 border-0 py-2 px-5 focus:outline-none hover:bg-black rounded">
      <a href={project.html_url}>{"Link To Project"}</a>
    </button>
  </div>
))}

            </div>
          </div>
        </section>
        <Footer />
      </div>
    );
  }
  
export default Projects;